import * as React from 'react';
import Avatar from '@mui/material/Avatar';
export interface AvatarProps  {
  src?: any;
  classname?:string;
}

const AvatarImage = (props: AvatarProps) => {
  return (
    <>
      <Avatar {...props}         sx={{ width: 24, height: 24 }}
 src={props.src} />
    </>
  );
};

export default AvatarImage;
